.cust-rev {
  background-image: url(/src/images/ice-cream-back.jpg);
  object-fit: cover;
  justify-content: center;
}

.cust-rev-font-black {
  color: #131313;
}

.cust-rev-font-white {
  color: #fcfcfc;
}

.cust-rev-font {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 186%;

  text-align: center;
  letter-spacing: 0.04em;

  margin-bottom: 32px;
  @extend %align-justify-center;
}

.cust-rev-font-back {
  padding: 40px 75px;
  background-color: #84a178;
  border-radius: 24px;
}

.cust-rev-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.cust-rev-list {
  list-style: none;
  margin: 0;
  padding-left: 0;
  text-decoration: none;
}

.cust-rev-swipe-pos {
  display: flex;
  flex-direction: row;
  gap: 18px;
  color: grey;
}

.cust-rev-swipe {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  color: #d9d9d9;
  background-color: #d9d9d9;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.cust-rev-swipe:hover {
  color: #d9d9d9;
  background-color: #84a178;
}

.cust-rev-swipe:focus {
  color: #d9d9d9;
  background-color: #84a178;
}

.glide-arrow-pos {
  background-color: transparent;
  border: none;
  color: black;
  font-size: 40px;

  display: inline-flex;
  justify-content: center;
  align-items: center;
}

.glide-arrow-pos:hover,
.glide-arrow-pos:focus {
  color: green;
}
